/**
 * poker-gto-trainer - Type Definitions
 * GTO
 */

/**
 * : 4
 * 100%
 */
export interface HandAction {
  allin: number;  //  (0-100)
  raise: number;  //  (0-100)
  call: number;   //  (0-100)
  fold: number;   //  (0-100)
}

/**
 * 
 * : "AA", "KQs", "72o"
 */
export type Hand = string;

/**
 * 
 */
export type HandActionType = 'allin' | 'raise' | 'call' | 'fold';

/**
 * : 169
 */
export interface CustomRange {
  id: string;
  name: string;
  description?: string;
  scenarioType?: '3bet' | '4bet' | 'preflop'; // 
  hands: Record<Hand, HandAction>;
  createdAt: string;
  updatedAt: string;
}

/**
 * 
 */
export type ScenarioType = 'preflop' | 'three-bet' | 'four-bet';

/**
 * 
 */
export type AppMode = 'debug' | 'training';

/**
 * 
 */
export type JudgmentMode = 'probabilistic' | 'frequency';

/**
 * 
 */
export interface TrainingSettings {
  rangeId: string;
  rangeName: string;
  coverage: number; // 0-100
  judgmentMode: JudgmentMode;
  totalQuestions: 50;
}

/**
 * 
 */
export interface TrainingQuestion {
  hand: Hand;
  correctActions: HandAction;
  userAnswer?: HandActionType;
  isCorrect?: boolean;
  expectedProbability?: number; // 
}

/**
 * 
 */
export interface TrainingResult {
  settings: TrainingSettings;
  totalQuestions: number;
  correctAnswers: number;
  score: number; // 0-100
  actionStats: Record<HandActionType, { correct: number; total: number }>;
  questions: TrainingQuestion[];
  completedAt: string;
}

/**
 * 
 */
export interface FrequencyTracker {
  [hand: string]: {
    expected: Record<HandActionType, number>;
    actual: Record<HandActionType, number>;
    count: number;
  };
}

/**
 * 
 */
export type Position = 'UTG' | 'MP' | 'CO' | 'BTN' | 'SB' | 'BB';

/**
 * : 
 */
export interface RangeScenario {
  id: string;
  type: ScenarioType;
  position: Position;
  vsPosition?: Position;  // 3bet/4bet
  range: CustomRange;
  isGTO: boolean;        // GTO
}

/**
 * 
 */
export interface ValidationResult {
  isValid: boolean;
  total: number;
  error?: string;
}

/**
 * 
 */
export interface Question {
  id: string;
  hand: Hand;
  scenario: RangeScenario;
  correctAction: HandAction;
}

/**
 *  - 
 */
export interface Answer {
  questionId: string;
  userAction: HandActionType;
  isCorrect: boolean;
  timestamp: string;
}

/**
 * 
 */
export interface LearningSession {
  id: string;
  scenarioId: string;
  questions: Question[];
  answers: Answer[];
  startedAt: string;
  completedAt?: string;
  score?: number;
}

/**
 * 
 */
export interface TrainingHistoryRecord {
  id: string;
  result: TrainingResult;
  timestamp: string;
}

/**
 * 
 */
export interface HandStats {
  hand: Hand;
  totalAttempts: number;
  correctCount: number;
  accuracy: number;
  lastAttempt: string;
}

/**
 * 
 */
export interface RangeStats {
  rangeId: string;
  rangeName: string;
  totalSessions: number;
  averageScore: number;
  bestScore: number;
  totalQuestions: number;
  lastPlayed: string;
}

/**
 * 
 */
export const StorageKey = {
  CUSTOM_RANGES: 'poker-gto-trainer:custom-ranges',
  LEARNING_HISTORY: 'poker-gto-trainer:learning-history',
  USER_PREFERENCES: 'poker-gto-trainer:user-preferences',
  APP_MODE: 'poker-gto-trainer:app-mode',
  TRAINING_SETTINGS: 'poker-gto-trainer:training-settings',
  TRAINING_HISTORY: 'poker-gto-trainer:training-history',
} as const;
